<template>
  <div class="confirmOrder mescroll">
    <van-tabs v-model="active" title-active-color="#ee0a24" @change="tabChange"> 
      <van-tab title="自提">
        <div class="address">
          <van-icon name="manager-o" size="20" color="#999" />
          <div class="text">
            <van-field v-model="username2" clearable placeholder="请输入姓名" />
          </div>
          <van-icon name="phone-circle-o" size="20" color="#999" />
          <div class="text">
            <van-field v-model="usermobile2" clearable placeholder="请输入手机号" />
          </div>
        </div>
      </van-tab> 
      <van-tab title="送货上门">
        <div class="address" @click="toAddress()">
          <img class="left" src="../../assets/img/icon/dizhi.png" alt />
          <div class="mid">
            <div v-if="address.areaCode">
              <p class="name">
                收货人:{{address.userName2}}
                <span>{{address.userMobile}}</span>
              </p>
              <p class="addr">收货地址:{{(address.areaNames?address.areaNames.replace(/\//g,''):'') + address.addrAddr}}</p>
            </div>
            <div v-else>
              <p style="color:#999;font-size:13px;">请添加收货地址</p>
            </div>
          </div>
          <img class="right" src="../../assets/img/icon/left.png" alt />
        </div>
      </van-tab>
    </van-tabs> 
    <div class="line"></div>
    <div class="list">
      <!-- <p class="title">{{goodsList.length?goodsList[0].company_name:''}}-{{goodsList.length?goodsList[0].shop_info_name:''}}</p> -->
      <div class="item" v-for="(item,index) in goodsList" :key="index">
        <van-swipe-cell :right-width="65" :disabled="goodsList.length>1?false:true">
          <div class="flex-goods">
            <div class="item-pic flex-1">
              <img :src="$utils.dealImg(item.gimg)" />
            </div>
            <div class="flex-2">
              <div class="title">{{item.gname}}</div>
              <div class="parameter" v-if="item.platform_goods_spec_value_names">{{item.platform_goods_spec_value_names.replace(','," ")}}</div>
              <div class="flex info">
                <div class="price">￥{{item.goutPrice.toFixed(2)}}</div>
                <div class="stepper">
                  <van-stepper v-model="item.num" @change="onChange(item,index)" @blur="blur" />
                </div>
              </div>
            </div>
          </div>
          <div slot="right" class="del" @click="del(index)">移除</div>
        </van-swipe-cell>
      </div>
      <van-cell title="物流" v-if="active == 1" is-link :value="expressage || '请选择'" @click="actionShow = true" />
      <van-cell title="优惠券" is-link :value="couponDetail.ytitle || '请选择'" @click="toCouponList" />
      <div class="flex" style="margin:10px 0 0;">
        <div class="flex-1">备注</div>
        <div class="flex-2">
          <van-field v-model="remark" type="textarea" placeholder="备注" input-align="right" />
        </div>
      </div>
    </div>
    <div class="orderInfo">
      <div class="flex">
        <div class="flex-1">商品数量</div>
        <div class="flex-2">{{allNum}}</div>
      </div>
      <div class="flex" v-if="couponDetail.ymoney">
        <div class="flex-1">优惠合计</div>
        <div class="flex-2">￥{{couponDetail.ymoney.toFixed(2)}}</div>
      </div>
      <div class="flex" v-if="freight">
        <div class="flex-1">运费</div>
        <div class="flex-2">￥{{freight.toFixed(2)}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">商品合计</div>
        <div class="flex-2">￥{{allPrice.toFixed(2)}}</div>
      </div>
     <div style="margin:0 -10px;">
        <van-cell
          title="支付方式："
          :value="payTypeText"
          value-class="color"
          @click="payTypeShow = true"
          is-link
        />
      </div> 
    </div>
    <div class="bottom">
      <div class="left">
        实付款：
        <span class="font">¥</span>
        <span class="price">{{payPrice.toFixed(2)}}</span>
      </div>
      <div class="btn" @click="submit">确认订单</div>
    </div>
    <van-popup v-model="popupShow">
      <div class="popup_wrap">
        <div class="popup_icon">
          <img src="../../assets/img/icon/cha-3.png" />
        </div>
        <div class="popup_content">
          <div class="title">
            支付
            <span>{{text}}！</span>
          </div>
          <div class="text">您可以在60分钟内前往我的订单进行支付，超过时长的订单将失效</div>
        </div>
        <div class="popup_btns">
          <div class="popup_btn" @click="cancel">取消</div>
          <div class="popup_btn go" @click="go">立即前往</div>
        </div>
      </div>
    </van-popup>
    <van-popup v-model="actionShow" position="bottom">
      <van-picker show-toolbar title="物流方式" value-key="sName" :columns="columns" @cancel="onCancel" @confirm="onSelect" />

    </van-popup>
    <van-popup v-model="payTypeShow" position="bottom">
      <div class="popup">
        <div class="popup-top">支付账户选择</div>
        <div class="popup-mid">
          <van-radio-group v-model="payType">
            <van-cell-group>
              <van-cell
                title="微信支付"
                :icon="require('../../assets/img/pic/wxPay.png')"
                clickable
                @click="choosePayType('1')"
              >
                <template #right-icon>
                  <van-radio name="1" />
                </template>
              </van-cell>
              <van-cell
                title="余额支付"
                :icon="require('../../assets/img/pic/accountPay.png')"
                clickable
                @click="choosePayType('2')"
              >
                <template #right-icon>
                  <van-radio name="2" />
                </template>
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </div>
      </div>
    </van-popup>
    <van-popup v-model="agreementShow">
      <div class="agreementMid">
        <div class="close" @click="agreementShow=false">×</div>
        <div v-html="agreement">{{agreement}}</div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import {
  SwipeCell,
  SwitchCell,
  Popup,
  Stepper,
  Dialog,
  Field,
  Cell,
  Picker,
  Tabs,
  Tab,
  Icon,
  RadioGroup,
  Radio,
  Checkbox, 
} from "vant";
import {
  getAddressList,
  submitOrder,
  reviseCartNumber,
  getlogisticList,
  getFreight,
  getPage
} from "@/api";
export default {
  components: {
    [SwipeCell.name]: SwipeCell,
    [Popup.name]: Popup,
    [SwitchCell.name]: SwitchCell,
    [Dialog.name]: Dialog,
    [Field.name]: Field,
    [Cell.name]: Cell,
    [Picker.name]: Picker,
    [Stepper.name]: Stepper,
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Icon.name]: Icon,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Checkbox.name]: Checkbox, 
  },
  data() {
    return {
      active: 0,
      select: "1",
      popupShow: false,
      min: 0,
      sec: 0,
      text: "失败",
      Interval: null,
      remark: "",
      goodsList: [],
      address: {},
      allNum: 0,
      freight: 0,
      allPrice: 0,
      payPrice: 0,
      expressage: "",
      expressageId: "",
      actionShow: false,
      columns: [],
      couponDetail: {},
      username2: "",
      usermobile2: "",
      payTypeShow: false,
      payType: "1",
      payTypeText: "微信支付",
      agreementShow: false,
      agreement: "",
      orderId: '',
      checked: true
    };
  },
  methods: {
    open() {
      if (!this.checked) {
        this.$toast("请选同意购买和托管协议");
        return;
      }
      this.agreementShow = true; 
    },
    onClose() {
      this.agreementShow = false; 
    },
    tabChange() {
      this.freight = 0;
      this.expressage = "";
      this.expressageId = "";
      this.calculate();
    },
    toCouponList() {
      sessionStorage.setItem("expressage", this.expressage);
      sessionStorage.setItem("expressageId", this.expressageId);
      sessionStorage.setItem("freight", this.freight);
      this.$router.push({
        name: "couponList",
        query: {
          type: 1,
          money: this.allPrice,
        },
      });
    },
    toAddress() {
      sessionStorage.setItem("expressage", this.expressage);
      sessionStorage.setItem("expressageId", this.expressageId);
      sessionStorage.setItem("freight", this.freight);
      this.$router.push({
        name: "myAddress",
        query: {
          type: 1,
        },
      });
    },
    onSelect(item, index) {
      this.expressage = item.sName;
      this.expressageId = item.sCode;
      this.loadFreight();
    },
    loadFreight() {
      let list = [];
      if (this.$route.query.type == "1") {
        this.goodsList.map(item => {
          list.push({
            gcode: item.id,
            gorderCount: item.num,
          });
        });
      } else {
        this.goodsList.map(item => {
          list.push({
            shoppingcartId: item.cartId,
          });
        });
      }
      getFreight({
        addressId: this.address.addrCode,
        orderDetailParamsList: list,
        scEcomHuoSettingId: this.expressageId,
      }).then(res => {
        if (res.code == 0) {
          this.freight = Number(res.data);
          this.calculate();
          this.actionShow = false;
        } else {
          this.$toast(res.msg);
        }
      });
    },
    onCancel() {
      this.actionShow = false;
    },
    blur() {
      document.body.scrollTop = 0;
    },
    onChange(item, index) {
      if (this.$route.query.type == "2") {
        reviseCartNumber({
          id: item.cartId,
          shopNum: item.num,
        }).then(res => {
          if (res.code == 0) {
            sessionStorage.setItem("goodsList", JSON.stringify(this.goodsList));
          } else {
            this.$toast(res.msg);
          }
        });
      } else {
        sessionStorage.setItem("goodsList", JSON.stringify(this.goodsList));
      }
      this.calculate();
    },
    calculate() {
      let allNum = 0,
        allPrice = 0;
      this.goodsList.map(item => {
        allNum += parseFloat(item.num);
        allPrice += parseFloat(item.num * item.goutPrice);
      });
      if (this.couponDetail.yamt > allPrice) {
        sessionStorage.removeItem("couponDetail");
        this.couponDetail = {};
      }
      this.allNum = allNum;
      this.allPrice = allPrice;
      this.payPrice = allPrice + this.freight - (this.couponDetail.ymoney || 0);
    },
    del(index) {
      let that = this;
      Dialog.confirm({
        message: "确定删除吗？",
      })
        .then(() => {
          that.goodsList.splice(index, 1);
          sessionStorage.setItem("goodsList", JSON.stringify(that.goodsList));
          that.calculate();
        })
        .catch(() => {
          // on cancel
        });
    },
    choosePayWay(index) {
      this.select = index;
    },
    choosePayType(type) {
      this.payType = type;
      this.payTypeText = type == "1" ? "微信支付" : "余额支付";
      this.payTypeShow = false;
    },
    submit() {
      if (this.active == 1 && !this.address.addrCode) {
        this.$toast("请选择地址");
        return;
      }
      if (this.active == 1 && !this.expressageId) {
        this.$toast("请选择物流方式");
        return;
      }
      if (this.active == 0 && !this.username2) {
        this.$toast("请输入提货人姓名");
        return;
      }
      if (this.active == 0 && !this.usermobile2) {
        this.$toast("请输入提货人手机号");
        return;
      }
      if (this.active == 0 && !this.$utils.isMobile(this.usermobile2)) {
        this.$toast("请输入正确的手机号");
        return;
      }
      if (!this.checked) {
        this.$toast("请先同意《购买和托管协议》");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      let list = [];
      if (this.$route.query.type == "1") {
        this.goodsList.map(item => {
          list.push({
            gcode: item.id,
            gorderCount: item.num,
          });
        });
      } else {
        this.goodsList.map(item => {
          list.push({
            shoppingcartId: item.cartId,
          });
        });
      }
      submitOrder({
        addressId: this.active == 1 ? this.address.addrCode : "",
        payType: this.payType,//'2', 
        orderDetailParamsList: list,
        remarks: this.remark,
        scEcomHuoSettingId: this.active == 1 ? this.expressageId : "",
        couponCode: this.couponDetail.id,
        username2: this.active == 0 ? this.username2 : "",
        usermobile2: this.active == 0 ? this.usermobile2 : "",
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.orderId = res.data.orderId;
          if(res.data.appid !=null)
          {
            this.weChatPay(
              res.data.appId,
              res.data.timeStamp,
              res.data.nonceStr,
              res.data.package,
              res.data.signType,
              res.data.paySign,
              res.data.orderId,
              res.data.orderNo,
              res.data.oorderWdate
            );
          }
          else{
            this.$toast("购买成功", "middle");
              setTimeout(() => {
                this.$router.replace({
                  name: "payResult",
                  query: {
                    orderId: res.data.orderId,
                    orderNo: res.data.orderNo,
                    payPrice:  this.payPrice,
                    payTime: res.data.oorderWdate
                  },
                });
              }, 500);
          }
          
        } else {
          this.$toast(res.msg);
        }
      });
    },
    weChatPay(
      appId,
      timeStamp,
      nonceStr,
      packageValue,
      signType,
      sign,
      orderId,
      orderNo,
      payTime
    ) {
      // console.log(appId, timeStamp, nonceStr, packageValue, signType, sign);
      var $this = this;
      if (typeof WeixinJSBridge == "undefined") {
        if (document.addEventListener) {
          document.addEventListener(
            "WeixinJSBridgeReady",
            onBridgeReady,
            false
          );
        } else if (document.attachEvent) {
          document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
          document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
        }
      } else {
        onBridgeReady();
      }
      function onBridgeReady() {
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest",
          {
            appId: appId, //公众号名称，由商户传入
            timeStamp: timeStamp, //时间戳，自1970年以来的秒数
            nonceStr: nonceStr, //随机串
            package: packageValue,
            signType: signType, //微信签名方式：
            paySign: sign, //微信签名
          },
          function(res) {
            console.log(res);
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              $this.$toast("支付成功", "middle");
              setTimeout(() => {
                $this.$router.replace({
                  name: "payResult",
                  query: {
                    orderId: orderId,
                    orderNo: orderNo,
                    payPrice: $this.payPrice,
                    payTime: payTime,
                  },
                });
              }, 500);
            } else if (res.err_msg == "get_brand_wcpay_request:cancel") {
              $this.show = false;
              $this.popupShow = true;
              $this.text = "取消";
              console.log("支付取消");
            } else if (res.err_msg == "get_brand_wcpay_request:fail") {
              $this.show = false;
              $this.popupShow = true;
              $this.text = "失败";
              // $this.$toast("支付失败，请稍后再试", "middle");
            }
            // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
          }
        );
      }
    },
    cancel() {
      this.$router.back();
    },
    go() {
      this.$router.replace({
        name: "orderDetail",
        query: { id: this.orderId },
      });
    },
    loadData() {
      if (sessionStorage.getItem("address")) {
        this.address = JSON.parse(sessionStorage.getItem("address"));
      } else {
        const loading = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true, // 禁用背景点击
        });
        getAddressList({
          pageNum: 1,
          pageSize: 10,
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.address = res.data.list.length ? res.data.list[0] : {};
          } else {
            this.$toast.fail(res.msg);
          }
        });
      }
      getlogisticList().then(res => {
        if (res.code == 0) {
          this.columns = res.data;
        } else {
          this.$toast(res.msg);
        }
      });
      this.expressage = sessionStorage.getItem("expressage") || "";
      this.expressageId = sessionStorage.getItem("expressageId") || "";
      this.freight = sessionStorage.getItem("freight")
        ? Number(sessionStorage.getItem("freight"))
        : 0;
      sessionStorage.removeItem("expressage");
      sessionStorage.removeItem("expressageId");
      sessionStorage.removeItem("freight");
      this.couponDetail =
        JSON.parse(sessionStorage.getItem("couponDetail")) || {};
      this.goodsList = JSON.parse(sessionStorage.getItem("goodsList"));
      this.calculate();
      getPage({ id: "7517681AE3D7486D9B1566AEABA59F1D" }).then(res => {
        if (res.code == "0") { 
           this.agreement = res.data.tMsg;
          //  this.$toast("欢迎购买星际诚汇矿机，请认真阅读购买和托管协议。");
        } else {
          this.$toast(res.msg);
        }
      });
    },
  },
  mounted() {
    document.title = "提交订单";
    this.loadData();
    this.username2 = sessionStorage.getItem("userName2");
    this.usermobile2 = sessionStorage.getItem("userMobile2");
    // let that = this,
    //   time = 15 * 60;
    // this.Interval = setInterval(() => {
    //   if (time > 0) {
    //     time--;
    //     that.min =
    //       parseInt(time / 60) > 9
    //         ? parseInt(time / 60)
    //         : "0" + parseInt(time / 60);
    //     that.sec =
    //       parseInt(time % 60) > 9
    //         ? parseInt(time % 60)
    //         : "0" + parseInt(time % 60);
    //   } else {
    //     clearInterval(that.Interval);
    //     that.$router.back();
    //   }
    // }, 1000);
  },
  // beforeDestroy() {
  //   clearInterval(this.Interval);
  // }
};
</script>
<style lang="scss" scoped>
.confirmOrder {
  background: #fff;
  .address {
    display: flex;
    align-items: center;
    padding: 10px;
    .left {
      width: 20px;
      margin-right: 10px;
    }
    .mid {
      flex: 2;
      margin-right: 10px;
      .name {
        font-weight: bold;
        line-height: 30px;
        span {
          float: right;
        }
      }
      .addr {
        color: #999;
        font-size: 15px;
      }
    }
    .right {
      width: 10px;
    }
  }
  .line {
    border-bottom: 5px solid #eee;
  }
  .list {
    border-bottom: 1pt solid #efefef;
    .title {
      padding: 5px 10px;
      font-weight: bold;
      line-height: 30px;
    }
    .flex {
      display: flex;
      padding: 0 10px 10px;
      .flex-1 {
        flex: 1;
        color: #999;
      }
      .flex-2 {
        flex: 2;
        line-height: 25px;
      }
    }

    .item {
      height: 120px;
      overflow: hidden;
      padding: 5px 10px;
      .flex {
        display: flex;
        align-items: center;
        padding: 0;
        .flex-2 {
          flex: 2;
        }
      }
      .flex-goods {
        display: flex;
        padding: 0;
        .flex-2 {
          flex: 2;
          position: relative;
        }
      }
      .del {
        width: 100%;
        margin: 50px auto 0;
      }
      .item-pic {
        margin-right: 10px;
        width: 120px;
        img {
          width: 100%;
          height: 120px;
        }
      }
      .title {
        font-size: 16px;
        line-height: 25px;
        padding: 0;
        max-height: 50px;
        font-weight: normal;
        text-overflow: -o-ellipsis-lastline;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
      .parameter {
        color: #999;
        font-size: 15px;
        line-height: 20px;
        margin: 10px 0;
      }
      .info {
        position: absolute;
        bottom: 0;
        width: 100%;
        .price {
          font-size: 15px;
          font-weight: 600;
          color: #333;
          flex: 2;
        }
        .stepper {
          margin-right: 5px;
        }
      }
    }
  }
  .orderInfo {
    padding: 10px 15px 60px;
    .flex {
      line-height: 30px;
      color: #999;
      font-size: 16px;
      display: flex;
      .flex-2 {
        flex: 2;
        text-align: right;
        color: #333;
      }
    }
    .allPrice {
      text-align: right;
      font-size: 20px;
      font-weight: bold;
      color: #eb5252;
      span {
        font-size: 30px;
        font-weight: bold;
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: 50px;
    left: 0;
    width: calc(100% - 10px);
    background: #fff;
    padding: 0 0 0 10px;
    display: flex;
    height: 50px;
    line-height: 50px;
    border-top: 1pt solid #efefef;
    z-index: 2;
    .left {
      flex: 2;
      .font {
        color: #eb5252;
        font-size: 13px;
        margin-right: 3px;
      }
      .price {
        color: #eb5252;
        font-size: 18px;
        font-weight: bold;
      }
    }
    .btn {
      width: 120px;
      background: #eb5252;
      color: #fff;
      text-align: center;
      font-size: 13px;
    }
  }
  .van-popup {
    overflow-y: visible !important;
    background: #fff;
  }

  .popup-line div {
    background: #e9e9e9;
    width: 30px;
    height: 3px;
